<template>
  <div class="amap-page-container">
    <!-- <van-search
      v-model="value"
      show-action
      label="地址"
      placeholder="请输入搜索关键词"
      @search="onSearch"
    >
      <template #action>
        <div @click="onSearch">搜索</div>
      </template>
    </van-search> -->

    <!-- 搜索条件 是个对象 属性是city城市名，citylimit：false； 搜索回调函数 -->
    <el-amap-search-box
      class="search-box"
      :search-option="searchOption"
      :on-search-result="onSearchResult"
      :center="center"
    >
    </el-amap-search-box>

    <!-- 地图 center 是为了更改定位后地图中心-->
    <el-amap
      class="amap-box"
      vid="amapDemo"
      style="position: relative; bottom: 45px"
      :center="center"
      :zoom="zoom"
      :plugin="plugin"
    >
      <!-- 标记点 添加N个人-->
      <el-amap-marker
        v-for="marker in markers"
        v-bind:key="marker.id"
        :position="marker.position"
        :events="marker.events"
        :content="marker.content"
      >
      </el-amap-marker>
      <el-amap-info-window
        v-if="window"
        :position="window.position"
        :visible="window.visible"
        :content="window.content"
        :offset="window.offset"
      >
      </el-amap-info-window>
      <!-- 圆 -->
      <el-amap-circle
        vid="circle"
        :center="center"
        :radius="circle.radius"
        fill-opacity="0.2"
        strokeColor="#38f"
        strokeOpacity="0.8"
        strokeWeight="1"
        fillColor="#38f"
      ></el-amap-circle>
    </el-amap>
  </div>
</template>
 
<script>
//import { getMapDistance } from "../../components/common";

export default {
  data() {
    let self = this;
    return {
      searchOption: {
        city: "全国",
        citylimit: false,
      },
      value: "",
      center: [0, 0], //圆心位置
      pointMarkers: [], // 数据储存
      markers: [],
      windows: [],
      window: "",
      circle: {
        radius: 1000, //圆半径，单位:米
      },
      input: "", //input内容
      zoom: 14, //地图缩放
      loaded: false,
      plugin: [
        {
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 100, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //显示定位按钮，默认：true
          buttonPosition: "RB", //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: false, //定位成功后用圆圈表示定位精度范围，默认：true   //苹果有效
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true, //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
          extensions: "all",
          pName: "Geolocation",
          events: {
            init(o) {
              //定位成功 自动将circle移到定位点
              o.getCurrentPosition((status, result) => {
                console.log(result.position.lng);
                console.log(result.position.lat);
                //console.log(result);
                if (result && result.position) {
                  self.center = [result.position.lng, result.position.lat];
                  self.loaded = true;
                } else {
                  console.log(`定位失败`);
                }
              });
              console.log(o);
            },
          },
        },
        {
          //地图工具条
          pName: "ToolBar",
        },
        {
          //左下角缩略图插件 比例尺
          pName: "Scale",
        },
      ],
    };
  },
  methods: {
    onSearch() {
      alert("sda");
    }, //点击搜索后触发的事件
    onSearchResult(pois) {
      console.log(pois);
      this.input = document.querySelector(".search-box-wrapper input").value; //input 内容
      this.center = [pois[0].lng, pois[0].lat]; //选择了第一个值
    },
    point() {
      let that = this;
      //console.log(this.pointMarkers);
      this.pointMarkers.forEach((item, index) => {
        that.markers.push({
          position: [item.lng, item.lat],
          icon: item.icon,
          events: {
            click() {
              that.windows.forEach((window) => {
                window.visible = false;
              });
              that.window = that.windows[index];
              that.$nextTick(() => {
                that.window.visible = true;
              });
            },
          },
        });
        that.windows.push({
          position: [item.lng, item.lat],
          content:
            '<div class="windows-text">' +
            '<div class="text-map">' +
            "<p>" +
            "烟感报警-方形测试" +
            "</p>" +
            "报警状态：" +
            item.lng +
            "<br>" +
            '<div class="bottom">' +
            '<button onclick="getVideo()">' +
            "查看视频" +
            "</button>" +
            "</div>" +
            "</div>" +
            "</div>",
          offset: [5, -15],
          visible: false,
        });
      });

      console.log(this.markers);
    },
  },
  mounted() {
    // 姑且N为2
    // 这样地图上就添加了两个人
    this.pointMarkers = [
      {
        lng: 121.34351,
        //content: "<div>0</div>",
        lat: 31.14959,
      },
      {
        lng: 121.34387,
        //content: "<div>1</div>",
        lat: 31.14989,
      },
    ];
    this.point();
    // var distance = getMapDistance(
    //   this.pointMarkers[0].position[0],
    //   this.pointMarkers[0].position[1],
    //   this.pointMarkers[1].position[0],
    //   this.pointMarkers[1].position[1]
    // );
    //console.log(`distance:${distance}`); // distance:7.9619

    // 模拟实时更新位置
    // 开启一个1s的轮训，每个人的经纬度都自增0.00001
    // const step = 0.00001;
    // setInterval(() => {
    //   this.pointMarkers.forEach((marker) => {
    //     marker.position = [
    //       marker.position[0] + step,
    //       marker.position[1] + step,
    //     ];
    //   });
    // }, 1000);
    // console.log(window.sessionStorage.id); //可以获取到经纬度
  },
};
</script>
 
<style scoped>
.amap-page-container {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100;
}

.el-vue-search-box-container {
  width: 100%;
}
</style>
