import Vue from 'vue'
import Router from 'vue-router'

//import HelloWorld from '@/components/HelloWorld'
import Login from '@/views/login/login' // 导入我们编写的vue组件Home 
import Register from '@/views/register/register' // 导入我们编写的vue组件Home 

import Home from '@/views/home/home'
import Test from '@/views/test/test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    }, {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    // 配置路由地址
    {
      path: '/test',
      name: 'test',
      component: Test
    }
  ]
})